<!-- 预约中心首页 -->
<template>
	<div class="container">
		<el-row class="top-statis-list">
			<el-col :span="8" v-for="(item,index) in statis" :key="index">
				<div class="top-statis">
					<div class="top-statis-name">{{item.name}}</div>
					<div class="top-statis-data">{{item.data}}</div>
					<img src="../../../assets/image/spume.png" alt="">
				</div>
			</el-col>
		</el-row>
		<el-row :style="{'padding-top':'32px'}">
			<el-col :lg="15" :span="24" :style="{'padding':'0 16px'}">
				<div class="box">
					<div class="flex-between order-title">
						<div class="order-title-name">预约统计</div>
						<ul>
							<li v-for="(item,index) in type" :class="item.active?'active':''" @click="typeChange(index)">{{item.text}}</li>
						</ul>
						<div>
							<order-time @time-change="timeSelected"></order-time>
						</div>
					</div>
					<order-calendar :tableData="orderList" :current="time"></order-calendar>
				</div>
			</el-col>
			<el-col :lg="9" :span="24" :style="{'padding':'0 16px'}">
				<div class="box bight">
					<div class="flex-between order-title">
						<div class="order-title-name">预约记录曲线</div>
					</div>
					<div id="order-record"></div>
				</div>
				<div class="box bight">
					<div class="flex-between order-title">
						<div class="order-title-name">预约信息</div>
					</div>
					<div class="order-msg-list">
						<ul>
							<li v-for="(item,index) in orderMsg" :key="index">
								<img src="http://cdn.admui.com/demo/pjax/2.0.0/images/avatar.svg" alt="">
								<div class="order-msg-list-name">{{item.userName}}</div>
								<div class="order-msg-list-detail">预约了一堂{{item.courseOrder==8002?'同步课':'录播课'}}</div>
								<div class="order-msg-list-time">{{item.courseTime}}</div>
							</li>
						</ul>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
import calendar from '../../../components/calendar'
import time from '../../../components/time-selected'
import echarts from 'echarts'
export default {
    data() {
        return {
            statis: [{
                name: '今日预约',
                data: 2
            }, {
                name: '本周预约',
                data: 18
            }, {
                name: '累计预约',
                data: 241
            }],
            orderMsg: [],
            orderList: [],
            type: [{
                id: 8000,
                text: '全部',
                active: true
            }, {
                id: 8002,
                text: '同步课',
                active: false
            }, {
                id: 8001,
                text: '录播课',
                active: false
            }, {
                id: 4,
                text: '会议',
                active: false
            }],
            time: new Date(),
            option: {
                xAxis: {
                    type: 'category',
                    data: [],
                    boundaryGap: false,
                    axisLine: {
                        show: false
                    }
                },
                yAxis: {
                    type: 'value',
                    boundaryGap: false,
                    show: false
                },
                grid: {
                    left: 20,
                    right: 20,
                    top: 0,
                    bottom: 20
                },
                series: [{
                    data: [],
                    type: 'line',
                    smooth: true,
                    itemStyle: {
                        color: '#4C84FF'
                    },
                    areaStyle: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: '#4C84FF'
                        }, {
                            offset: 1,
                            color: '#ffffff'
                        }])
                    }
                }]
            }
        }
    },
    components: {
        'order-calendar': calendar,
        'order-time': time
    },
    mounted() {

    },
    created() {
        this.indexSearch();
    },
    methods: {
        typeChange(index) {
            this.type.map((v, i) => {
                if (index == i) {
                    v.active = true
                } else {
                    v.active = false
                }
            })
            this.canlendarSearch();
        },
        indexSearch() {
            this.$api.orderIndex().then(res => {
                this.statis[0].data = res.data.todayCount;
                this.statis[1].data = res.data.weekCount;
                this.statis[2].data = res.data.allCount;
                this.orderMsg = res.data.courses;
                this.orderList = res.data.sections;
                let xAxis = [];
                let series = [];
                res.data.everyday.map(v => {
                    xAxis.push(v.courseDate.substring(5).replace('-', '/'))
                    series.push(v.num)
                })
                this.option.xAxis.data = xAxis
                this.option.series[0].data = series
                let myChart = echarts.init(document.getElementById('order-record'));
                myChart.setOption(this.option);
            })
        },
        timeSelected(value) {
            this.time = value;
            this.canlendarSearch();
        },
        canlendarSearch() {
            let data = {};
            this.type.map(v => {
                if (v.active) {
                    data.id = v.id
                }
            })
            data.date = this.$myLayer.timeFormat(this.time).substring(0, 10);
            this.$api.orderIndexCourse({
				date:data.date,
				order:data.id
			}).then(res => {
                 this.orderList = res.data;
            })
        }
    }
}
</script>

<style lang="scss" scoped="scoped">
	.container {
		padding: 0 16px;
		background-color: #F1F3F8;

		.box {
			background: rgba(255, 255, 255, 1);
			box-shadow: 0px 5px 20px 0px rgba(23, 38, 71, 0.05);
			border-radius: 6px;
			padding: 22px 24px;
			width: 100%;

			&.bight {
				height: 399px;
			}

			.order-msg-list {
				height: calc(100% - 47px);

				ul {
					height: 100%;

					li {
						display: flex;
						align-items: center;
						height: 20%;

						img {
							width: 40px;
							height: 40px;
							margin-right: 20px;
							border-radius: 100%;
						}

						.order-msg-list-name {
							width: 45px;
							margin-right: 42px;
						}

						.order-msg-list-detail {
							width: calc(100% - 300px);
							margin-right: 32px;
						}

						.order-msg-list-time {
							width: 120px;
						}
					}
				}
			}

			#order-record {
				width: 100%;
				height: 300px;
			}

			.flex-between {
				&.order-title {
					align-items: center;
					margin-bottom: 28px;

					.order-title-name {
						color: #55595C;
						font-weight: bold;
					}
				}

				ul {
					display: flex;

					li {
						height: 28px;
						line-height: 28px;
						padding: 0 11px;
						cursor: default;

						&.active {
							background-color: #4C84FF;
							border-radius: 14px;
							color: #FFFFFF;
						}
					}
				}
			}
		}

		.top-statis {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 16px;
			height: 140px;

			&:first-child {
				background: linear-gradient(to top right, #8EC9F8, #4C84FF);
			}

			.top-statis-name {
				color: #FFFFFF;
				position: absolute;
				top: 22px;
				left: 24px;
			}

			.top-statis-data {
				color: #FFFFFF;
				font-size: 48px;
			}

			img {
				position: absolute;
				right: 0;
				top: 0;
				bottom: 0;
				height: 140px;
				width: 118px;
			}
		}

		.top-statis-list {
			padding-top: 32px;

			.el-col {
				&:nth-child(2) {
					.top-statis {
						background: linear-gradient(to top right, #FFBE96, #FE7096);
					}
				}

				&:nth-child(3) {
					.top-statis {
						background: linear-gradient(to top right, #83D9D2, #07CDAE);
					}
				}
			}
		}
	}
</style>
